import React from 'react';
import {Link} from 'react-router-dom';
import './Tiles.css';

function TileLink(props) {
  return (
    <Link className='Tile' to={props.to}>
      {props.children}
    </Link>
  );
}

export function TileButton(props) {
  return (
    <TileLink to={props.to}>
    <>
      <h3 className='TileButton__Label'>{props.label}</h3>
      <div className='TileButton__IconContainer'>
        <span className={`TileButton__Icon ${props.icon}`}/>
      </div>
    </>
    </TileLink>
  );
}

export function ThumbnailTile(props) {
  const to = props.to || '/channel/1';
  const label = props.label || 'Channel';

  return (
    <TileLink to={to}>
    <div>
      <div className='ThumbnailTile__Label'>
        {label}
      </div>
    </div>
    </TileLink>
  );
}
