import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './CreateChannel.css';

const CATEGORIES = [
  'aab','bab','cab','dab','eab','fab','gab','hab','iab','jab','kab','lab','mab',
  'abc','bbc','cbc','dbc','ebc','fbc','gbc','hbc','ibc','jbc','kbc','lbc','mbc',
  'acd','bcd','ccd','dcd','ecd','fcd','gcd','hcd','icd','jcd','kcd','lcd','mcd',
];
const CategoryEntry = props => {
  return (
    <div
      className={
        `CategoryEntry ${props.selected ? 'CategoryEntry--Selected' : ''}`
      }
      onClick={() => {props.onClick(props.category)}}>
      {props.category}
    </div>
  );
}

export default class CreateChannel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      query: '',
      categories: new Set(),
      showCategoriesList: false,
    }

    this.toggleCategory = this.toggleCategory.bind(this);
  }

  render() {
    const {query, categories, showCategoriesList} = this.state;

    return (
      <div className='Container'>
        <input
          placeholder='Name your channel'/>
        <h2>Add some categories</h2>
        <div className='CategoryContainer'>
          <input
            className='SearchBar'
            placeholder='Search categories...'
            type='text'
            onChange={this.search.bind(this)} />
          <div className='CategoryList'>
          {
            CATEGORIES.filter(
              category => query ?
                category.indexOf(query) >= 0 : true)
                        .map(category =>
                          <CategoryEntry
                            key={category}
                            category={category}
                            selected={categories.has(category)}
                            onClick={this.toggleCategory}/>
                        )
          }
          </div>
        </div>
        <button>Create Channel</button>
      </div>
    );
  }

  search(evt) {
    const query = evt.target.value;
    const showCategoriesList = query.length > 0;
    this.setState({query, showCategoriesList});
  }

  toggleCategory(category) {
    const {categories} = this.state;
    if (categories.has(category)) {
      categories.delete(category);
    } else {
      categories.add(category);
    }
    this.setState({categories});
  }
}
